export default class CustomInterface {
    constructor(data = {}) {
        // Inicializa as propriedades comuns 'created' e 'updated'
        this.created = data.created ? new Date(data.created) : new Date();  // Data de criação (pode vir da API ou ser a data atual)
        this.updated = data.updated ? new Date(data.updated) : new Date();  // Data de última atualização (pode vir da API ou ser a data atual)
    }

    // Método que transforma a instância da interface no formato para ser enviado à API
    toApiPayload() {
        return {
            created: this.created.toISOString(),  // Converte a data para o formato ISO
            updated: this.updated.toISOString(),  // Converte a data para o formato ISO
        };
    }
}
