import CustomInterface from './CustomInterface';

export default class CobrancaInterface extends CustomInterface {
    constructor(data = {}) {
        // Chamando o construtor da classe base para inicializar 'created' e 'updated'
        super(data);

        // Inicializações específicas para CobrancaInterface
        this.id_cobranca = data.id_cobranca || '';
        this.descricao = data.descricao || '';
        this.valor = data.valor || 0.0;
        this.data = data.data || new Date(); // Data padrão para o campo 'data'
        this.pago = data.pago || false;
        this.cliente_id = data.cliente_id || '';
    }

    static fromApiResponse(data) {
        return new CobrancaInterface(data);
    }

    toApiPayload() {
        return {
            ...super.toApiPayload(), // Inclui 'created' e 'updated' da classe base
            id_cobranca: this.id_cobranca,
            descricao: this.descricao,
            valor: this.valor,
            data: this.data,
            pago: this.pago,
            cliente_id: this.cliente_id,
        };
    }
}
